.doctor{
  padding-bottom: 10px;
  .doctor__content{
    background-color: $color-background;
    margin-top: 30px;
    margin-bottom: 30px;
    .doctor__content__pic{
      height: 50%;
      background-color: red;
      .doctor-img{
        width: 100%;
        max-height: 350px;
        border: 1px solid #EEEEEE;
      }

    }
    .doctor__content__text{
      border: 1px solid #EEEEEE;
    }
    .content__text-box{
      width: 85%;
      margin: 20px auto 20px auto;
      &>span{
        color: $color-text;
      }
      &>h5{
        color: $color-text;
        opacity: 0.8;
        margin-top: -10px;
      }
    }

  }
}