
.appoint-header {
  background: none;
  margin-top: -75px;
  .appoint-header__box {
    height: 145px;
    background: $color-primary;
    position: relative;
    text-align: center;
  }
  .box-left {
    background-color: #859DFF;
    opacity: 0.8;
  }
  .box-center {
    background-color: #E5F853;
    opacity: 0.8;
  }
  .box-right {
    background-color: #FB99DE;
    opacity: 0.8;
  }
  h4{
    color: $color-background;

  }
}
.appointment__content{
}
.appointment__bg{
  background-image: url("/assets/img/doctor.png");
  background-repeat: no-repeat;
  background-size: cover;


  margin-top: 35px;
}
.border-top{
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid $color-text;
}
.appointment__content__left{
  border-right: 1px solid $color-text;
  padding: 30px 20px 30px 0;
}
.appointment__content__row{
  margin-top: 35px;
}
.appointment__content__right{
  padding-left: 20px;
}
.icon-circle{
  margin: 10px 0 10px 10px;
  float: left;
  border: medium none;
  border-radius: 100px;
  display: inline-block;
  height: 80px;
  line-height: 80px;
  width: 80px;
  background-color: $color-primary;
}
.appointment-icon{
  margin-top: 10px;
  padding: 25px;
  border-radius: 50%;
  background-color: $color-primary;
  color: $color-background;
}
.appointment-title{
  margin-top: -5px;
  margin-bottom: 10px;
  width: 60px;
  height: 2px;
  background-color:  $color-text;
}