#header {

  .working-time {
    color: $color-title;
    padding-top: 2px;
  }
  .working-time-icon {
    background-color: $color-primary;
    color: $color-background;
    font-size: 2rem;
  }
  .header-icon-link{
    padding-top: 5px;
    text-align: right;
  }
  .header__menu-bg{
    background: $color-background;
    border: none;
  }
  .icon-top {
    background: $color-title;
    padding: 5px 6px;
    border-radius: 50% 50%;
    margin-left: 10px;
    &:hover {
      background: $color-background;
      color: $color-brand;
    }
  }
  .header__menu {

    .nav-style{
      height: 5em;
      background-color: $color-background;
      border: none !important;
    }
    .nav-style>li{
      height: 5em;
      padding: 15px 5px;
      border-top: 3px solid $color-background;
      &:hover{
        border-top: 3px solid $color-title;
        background-color: ghostwhite;
      }
      &.home{
        border-top: 3px solid $color-title;
      }
      a.home{
        background-color: $color-primary ;
        color: $color-background;
        padding: 5px;
      }
    }

    .nav-style>li>a{
      font-size: 1.3rem;
      padding: 5px;
    }

  }
  .header__logo {
    height: 50px;
    width: auto;
  }
  .hotline-box{
    padding: 3px ;
  }
  span.hotline-text{
    font-size: 1rem;
    color: $color-primary !important;
    margin-top: 10px;
  }
  span.hotline-number{
    font-size: 1.8rem;
    color: $color-primary !important;
  }
}

