.knowledge-bg{
  //background-image: url("/assets/img/doctor.png");
  background-color: #E9E9E9;
  background-repeat: no-repeat;
  background-size: cover;

}
.knowledge{

  .knowledge__content{
    height: 350px;
    background-color: $color-background;
    margin-top: 30px;
    margin-bottom: 20px;
    .knowledge__content__pic{
      height: 50%;
      background-color: red;
      position: relative;
      .knowledge-img{
        width: 100%;
        max-height: 175px;
      }
      .knowledge-date-create{
        position: absolute;
        width: 50px;
        height: 70px;
        background: $color-primary;
        right: -25px;
        top: 35%;
        border: 5px solid $color-background;
        color: $color-background;
        text-align: center;
        &>span{
          font-weight: 600;
          font-size: 17px;
        }
        &>p{
          font-weight: 600;
          padding-top: 7px;
        }
      }
    }
    .knowledge__content__text{
      width: 80%;
      margin: 0 auto;
      margin-top: 30px;
      &>span{
        color: $color-text;
      }
    }

  }
}