.btn-appointment{
  padding: 10px 20px;
  background-color: $color-primary;
  color: $color-background;
  border-radius: 0;
  border: medium none;
  font-weight: 600;
  box-shadow: $color-brand 0 1px 1px;

  &:hover{
    background-color: $color-primary;
    color: $color-background;
    box-shadow: $color-brand 0 5px 5px;
  }
}